import { GET, POST } from './index.js';

/**
 * 
 * @param  createFrom string  0 class | 1 person
 * @param groupInfo string 
 * @param groupName 
 * @param headImg 
 * @param isPublic 
 * @param userToken token 
 * @param userCode code
 * */ 
export const createNewGroup = (params) => 
  POST('/api/group/groupCreateOne.do', params);

/**
 * 
 * @param page 
 * @param selevtName keyword
 * @param type 0   1  | 2
 * @param userToken
*/
export const searchGroups = (params) => 
  POST('/api/group/selectGroup.do', params);

/**
 * 
 * @param groupId number ID
 * @param studentCode string code
 * @param studentId number id
 * */ 
export const addNumberToGroup = (params) => 
  POST('/api/group/addGroupMember.do', params);

/**
 * @param groupId id
 * */ 
export const getGroupDetail = (params) => 
  POST('/api/group/selectAllGroup.do', params);

/**
 * 
 * @param keyWord 
 * */ 

export const queryKnowledgeListByKeywords = (params) => 
  POST(`/api/student/knowBase/selectKnowBaseBykeys.do?keyWord=${params.keyWord}`);
