import React, { Component } from 'react';
import MainGuidePage from '../../MainGuidePage';
import SelectMenu from '../../../public/SelectMenu';
import ActiveItem from './ActiveItemCards/ActiveItem';
import { Button } from 'antd';
import AddEntryModal from '../../../public/AddEntryModal';
import './index.css';

const options = [
  {
    label: '',
    value: 'docs',
  },
  {
    label: '',
    value: 'knowledge',
  },
  {
    label: '',
    value: 'team',
  }
]

export default class Main extends Component {
  state = {
    isNew: true,
    newTemplateList: [],
    hasNoData: false,
    activeList: [],
    entries: [],
  }

  async componentDidMount() {
    const { newTemplateList, activeList } = this.state;
    // const res = await actions.userInfo.getUserInfos();
    const titleArr = ['', '', '', '', '', ''];
    const descArr = ['', '', '', '', '', '']
    for (let i = 0; i< 6; i++) {
      newTemplateList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: titleArr[i],
        desc: descArr[i],
      });
      activeList.push({
        id: `${i}8000${i + 1}${i + 2}`,
        title: '',
        courseName: '',
        groupName: '',
        knowledgeName: '',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '',
        behavior: 'update',
        content: '',
        // isNew: res && res?.groupId,
      });
    }

    this.setState({
      newTemplateList,
      activeList,
    })
  }

  SelectItems = values => {
    if (values.length === 0) {
      this.setState({
        hasNoData: true,
      })
    } else {
      this.setState({
        hasNoData: false,
      })
      console.log('');
    }
  }

  onEdit = id => {
    console.log(id, '');
    this.props.history.push(`/docDetail/edit/${id}`);
  }

  getMoreData = () => {
    const { activeList } = this.state;
    for (let i = 0; i < 6; i++) {
      activeList.push({
        id: `${i}8000${i + 1}${i + 4}`,
        title: '',
        courseName: '',
        groupName: '',
        knowledgeName: '',
        updateTime: '2020-2-22 20:42',
        changeAuthor: '',
        behavior: 'update',
        content: ''
      });
    };
    this.setState({
      activeList,
    })
  }

  onSureEntry = ({text, href}) => {
    const { entries } = this.state;
    entries.push({
      text,
      href,
    });

    this.setState({
      entries,
    });
  }

  render() {
    const { isNew, newTemplateList, hasNoData, activeList, entries } = this.state;

    return <>
      {
        isNew && newTemplateList.length > 0 && <MainGuidePage list={newTemplateList} />
      }
      <div className="dashboard-main-page">
        <div className="latest-new">
          <SelectMenu
            options={options} 
            onChange={(values) => this.SelectItems(values)}
          />
          {
            hasNoData && (
              <div></div>
            )
          }
          {
            !hasNoData && <>
              {activeList.map(item => <ActiveItem key={item.id} onEdit={() => this.onEdit(item.id)} activeItem={item}></ActiveItem>)}
              <Button 
                onClick={() => this.getMoreData()}
                style={
                  {
                    width: '100%', 
                    lineHeight: '40px', 
                    height: '60px',
                    border: 0,
                    boxShadow: '0px 3px 14px rgba(0,0,0,0.04)'
                  }
                }
              ></Button>
            </>
          }
        </div>
        <div className="right-area">
          <div className="today-support">
            
            <div className="support-image">
              <div className="actions-bar">
                <span></span>
                <span></span>
              </div>
            </div>
          </div>
          <div className="quickly-entry">
            
            <ul className="entries">
              {
                entries.map(item => {
                  const url = !item.href.includes('http') ? `http://${item.href}` : item.href;
                  return <li>
                  <a className="link" target="blank" href={url}>
                    {item.text}
                  </a>
                </li>
                })
              }
              <li>
                <a
                  href="javascript:;"
                  onClick={() => AddEntryModal.show({
                    onSure: this.onSureEntry
                  })
                }>
                  
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </>
  }
}