import React, { Component } from 'react';
import SharedTable from '../../../public/SharedTable';
import SearchBar from '../../../public/SearchBar';

import './index.css';
const courseList = [];
const titleArr = ['', '', '', '', '', ''];
const attrArr = ['', '', '', ''];
for (let i = 0; i < 6; i++) {
  const number = parseInt(Math.random() * 4);
  courseList.push({
    id: `2y7uy3u4${i}`,
    icon: () => <img src={require('../../../assets/images/logo.png')} />,
    courseName: titleArr[i],
    courseAttr: attrArr[number],
    courseDesc: '',
    courseTeacher: ['', '']
  })
};
export default class Course extends Component {
  constructor(props) {
    super(props);
    const columns = [
      {
        title: '',
        key: 'courseName',
        width: 320,
      },
      {
        title: '',
        key: 'courseAttr',
        width: 240,
      },
      {
        title: '',
        key: 'courseDesc',
        width: 360,
      },
      {
        title: '',
        key: 'courseTeacher',
        width: 160
      }
    ];

    this.state = {
      columns,
      courseList,
    }
  }

  onSearch = searchParams => {
    const newArr = [...courseList];
    this.setState({
      courseList: newArr.filter(item => item.courseName.includes(searchParams)),
    })
  }

  render() {
    const { columns, courseList } = this.state;
    return <div className="course-list">
      <SearchBar 
        onClick={(value) => this.onSearch(value)} 
        onSearch={values => this.onSearch(values)} 
        searchTitle="" 
        searchBtnText="" />
      <SharedTable parentPath="#/course/detail/" columns={columns} dataSource={courseList} />
    </div>
  }
}