import React, { Component } from 'react';
import PopPanel from '../PopPanel';
import { Form, Input } from 'antd';

class AddEntryModal extends Component {
  state = {
    text: undefined,
    href: undefined,
  }

  onChangeItem = (type, value) => {
    this.setState({
      [type]: value,
    });
  }

  beforOneSure = () => {
    const { text, href } = this.state;
    const { onSure } = this.props;

    if (text.trim() === '' || href.trim() === '') {
      return;
    }

    onSure({text, href});

  }

  render() {
    const { text, href } = this.state;
    return (
      <PopPanel
        title=""
        onOkCallBack={() => this.beforOneSure()}
      >
        <Form 
          layout="vertical"
        >
          <Form.Item label="">
            <Input value={text} onChange={(e) => this.onChangeItem('text', e.target.value)} placeholder="" />
          </Form.Item>
          <Form.Item label="">
            <Input value={href} onChange={(e) => this.onChangeItem('href', e.target.value)} placeholder="" />
          </Form.Item>
        </Form>
      </PopPanel>
    )
  }
}

export default PopPanel.wrapper(AddEntryModal);